.comment {
    background: #ededed;
    padding: 15px;
    list-style: none;
    max-width: 100%;
    cursor: pointer;
}

.comment div:first-child {
    margin-bottom: 10px;
}

.comment svg:last-child {
    margin: 10px 0 0 30px;
}

.comment_deleted {
    color: red;
    font-style: italic;
}